<template>
  <el-container class="outpatientsCs">
    <el-header>
      <el-input placeholder="请输入内容" v-model="input4">
        <i slot="suffix" @click="ss()" class="el-input__icon el-icon-search"></i>
      </el-input>
    </el-header>
    <el-main>
      <div>
        <el-tabs tab-position="left">
          <el-tab-pane v-for="(listDatas,Findex) in listData" :key="listDatas.indexa" :label="listDatas.id">
            <!--  -->
            <el-row
              ref="msgDiv"
              class="row avi"
              v-for="(listDatas2,index) in listDatas.address"
              :key="listDatas2.index"
            >
              <el-col :span="24">
                <div
                  v-for="(listDatas3) in listDatas2"
                  :key=" listDatas3.index"
                  class="grid-content bg-purple"
                  @click="listClick(Findex,index,listDatas3)"
                ><span>{{listDatas3}}</span><span class="J-right el-icon-arrow-right"></span></div>
              </el-col>
            </el-row>
          </el-tab-pane>
        </el-tabs>
      </div>
    </el-main>
  </el-container>
</template>
<style scoped>
/* @import "/src/assets/css/outpatient.css"; */
@import "../../assets/css/outpatient.css";
</style>
<script>
import axios from "axios";
export default {
  data() {
    return {
      input4: "",
      listData: []
    };
  },
  mounted() {
    this.httpA();
  },
  methods: {
    httpA() {
      axios.get("http://lc-7fHWOBLD.cn-n1.lcfile.com/7b5da17dc765a403d629/list.json").then(res => {
        console.log(res);
        // console.log(res.data[1].address);
        this.listData = res.data;
      });
    },
    //
    // 给当前显示科室添加点击事件
    listClick(Findex,index,listDatas3) {
      // console.log(this.$refs.testDom)
      // var lists = document.querySelectorAll(".avi")
      // console.log(lists);
      // this.$nextTick(()=>{ })
      // this.listData[Findex].address.splice(index, 1); //删除当前行
      console.log(this.listData[Findex].address[index]);
      console.log(listDatas3);
      alert(Findex+1+"排"+(index+1)+"列")

      this.$router.push({ name: 'bookingRegister', params: { userId: listDatas3 }});
    },
    ss(){
      alert(this.input4);
    }
  }
};
</script>
<style>
</style>


